
import Link from 'next/link';
import ProductCard from '../../components/ProductCard';
import RelatedProducts from '../../components/RelatedProducts';
import Image from 'next/image';
import { FaMinus, FaPlus } from 'react-icons/fa';

async function getProducts() {
  try {
    const res = await fetch('http://localhost:3000/api/products');
    if (!res.ok) {
      throw new Error('Failed to fetch products');
    }
    return res.json();
  } catch (error) {
    console.error('Error fetching products:', error);
    return [];
  }
}

export default async function ProductDetailPage({ params }) {
  const products = await getProducts();
  const product = products.find(p => p.id === Number(params.id));

  if (!product) {
    return (
      <div className="container mx-auto px-4 py-8 text-center">
        <h1 className="text-2xl font-bold text-red-500">محصول یافت نشد</h1>
        <Link href="/products" className="text-blue-600 hover:underline mt-4 inline-block">
          بازگشت به صفحه محصولات
        </Link>
      </div>
    );
  }

  // Add a default description if none exists
  product.description = product.description || 'توضیحاتی برای این محصول موجود نیست.';

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="flex flex-col md:flex-row gap-8 mb-12">
        {/* Product Image - Right Side */}
        <div className="md:w-1/2 bg-white p-4 rounded-lg shadow-md flex justify-center">
          <div className="relative w-full h-96">
            <Image
              src={product.imageUrl}
              alt={product.name}
              fill
              className="object-contain"
              sizes="(max-width: 768px) 100vw, 50vw"
            />
          </div>
        </div>

        {/* Product Details - Left Side */}
        <div className="md:w-1/2">
          <h1 className="text-2xl font-bold text-gray-800 mb-2">{product.name}</h1>
          
          <div className="flex items-center gap-3 mb-4">
            <div className="flex text-yellow-400">
              {[...Array(5)].map((_, i) => (
                <span key={i}>
                  {i < Math.floor(product.rating) ? '★' : '☆'}
                </span>
              ))}
            </div>
            <span className="text-sm text-gray-500">({product.reviews} نظر)</span>
          </div>

          <div className="mb-6">
            {product.discount > 0 && (
              <div className="flex items-center gap-3">
                <span className="text-lg font-bold text-red-600">
                  {new Intl.NumberFormat('fa-IR').format(product.price)} تومان
                </span>
                <span className="text-sm text-gray-500 line-through">
                  {new Intl.NumberFormat('fa-IR').format(product.originalPrice)}
                </span>
                <span className="text-xs bg-red-100 text-red-800 px-2 py-1 rounded">
                  {product.discount}% تخفیف
                </span>
              </div>
            )}
            {!product.discount && (
              <span className="text-lg font-bold text-gray-800">
                {new Intl.NumberFormat('fa-IR').format(product.price)} تومان
              </span>
            )}
          </div>

          {/* Quantity Selector */}
          <div className="mb-6">
            <label className="block text-gray-700 mb-2">تعداد:</label>
            <div className="flex items-center gap-2">
              <button className="p-2 bg-gray-200 rounded hover:bg-gray-300">
                <FaMinus />
              </button>
              <input 
                type="number" 
                min="1" 
                defaultValue="1"
                className="w-16 text-center border border-gray-300 rounded py-2"
              />
              <button className="p-2 bg-gray-200 rounded hover:bg-gray-300">
                <FaPlus />
              </button>
            </div>
          </div>

          <button className="w-full bg-blue-600 hover:bg-blue-700 text-white py-3 px-4 rounded flex items-center justify-center gap-2 transition-colors mb-6">
            <span>افزودن به سبد خرید</span>
          </button>

          {/* Product Description */}
          <div className="bg-gray-50 p-4 rounded-lg">
            <h2 className="text-lg font-bold mb-2">توضیحات محصول</h2>
            <p className="text-gray-700">{product.description}</p>
          </div>
        </div>
      </div>

      <RelatedProducts products={products} currentProductId={product.id} />
    </div>
  );
}